  flex: 1;
  background: #fff;
  padding: 20px;
  font-size: 15px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.countdown {
  font-size: 20px;
  color: #333;
}

.countdown span {
  font-weight: bold;
  margin: 0 5px;
}

/* SLIDESHOWS */
.slideshow-container {
  position: relative;
  max-width: 1080px;
  /* Set your desired width */
  max-height: 500px;
  margin: 0 auto;
  /* Center the slideshow container */
  overflow: hidden;

}

.slide {
  display: none;
}

.slide img {
  width: 100%;
  height: auto;
}

/* Navigation button styles */
.prev,
.next {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
  cursor: pointer;
  padding: 10px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
  font-size: 18px;
}

.prev {
  left: 10px;
}

.next {
  right: 10px;
}

/* NEWS HEADLINES */

.news-container {
  max-width: 600px;
  height: 454px;
  /* Adjust as per your container width */
  margin: 0 auto;
  /* Centers the container */
  padding: 20px;
  background-color: #f0f0f0;
  overflow-y: auto;
}
.news-container-title{
  font-size: 25px;
}

.news-headline {
  margin-bottom: 20px;
  border-bottom: 1px solid #ccc;
  padding-bottom: 10px;
  font-size: 20px;
}

.news-headline a {
  display: block;
  text-decoration: none;
  color: #333;

}

.news-image {
  max-width: 100%;
  /* Ensures the image fits within its container */
  height: auto;
  /* Allows the image to scale proportionally */
  display: block;
  /* Ensures the image does not create inline space */
  margin-bottom: 10px;
  /* Adds some space below the image */
}